* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  height: 70px;
  width: 100%;
  padding: 20px 100px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  position: relative;
  border-bottom: 1px solid #e5e5e7;

  .nav-header {
    .nav-logo {
      img {
        height: 25px;
      }
    }
  }

  .nav-links {
    display: flex;
    font-size: 16px;

    a {
      padding: 0px 7px 0px 10px;;
      text-decoration: none;
      font-weight: 600;
      color: #1b252c;
      font-size: 16px;

      letter-spacing: 0px;
      &:hover {
        background-color: rgba(0, 0, 0, 0.3);
      }
    }
  }

  #nav-check,
  .nav-btn {
    display: none;
  }
  .Right_Nav_section {
    .User_Part {
      display: flex;
    }
    .Search_Part {
      display: flex;
    }
    img {
      width: 22px;
      height: 22px;
    }

    .icon-wrapper {
      position: relative;
      float: left;
    }

    *.icon-blue {
      color: Chartreuse;
    }

    *.icon-grey {
      color: yellow;
    }

    i {
      text-align: center;
      vertical-align: middle;
    }

    .badge {
      background: #1b252c 0% 0% no-repeat padding-box;
      width: 26px;
      height: 26px;
      margin: 0;
      border-radius: 50%;
      position: absolute;
      top: -14px;
      right: -20px;
      padding: 6px;
      text-align: center;
      cursor: pointer;
      color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 600;
    }
  }
}

@media (max-width: 800px) {
  .navbar {
    padding: 20px 10px;
    width: 100%;
    background-color: #fff;
    display: block;
    position: relative;

    .nav-btn {
      display: inline-block;
      position: absolute;
      top: 0px;
      right: 0px;

      label {
        display: inline-block;
        width: 80px;
        height: 70px;
        padding: 25px;

        span {
          display: block;
          height: 10px;
          width: 25px;
          border-top: 3px solid #1b252c;
        }
      }
    }

    .nav-links {
      position: relative;
      display: block;
      text-align: center;
      width: 100%;
      background-color: white;
      transition: all 0.3s ease-in;
      overflow-y: hidden;
      top: 142px;
      right: 10px;
      margin-top: -120px;
      line-height: 1.8;
      a {
        display: block;
        line-height: 2.5;
        text-align: left;
      }
    }

    #nav-check {
      &:not(:checked) ~ .nav-links {
        height: 0px;
      }

      &:checked ~ .nav-links {
        height: calc(100vh - 220px);
        overflow-y: auto;
        width: 100%;
      }
    }

    .nav-links .loginBtn {
      padding: 10px 40px;
      margin: 20px;
      font-size: 18px;
      font-weight: bold;
      color: rgb(9, 14, 90);
    }
    .Right_Nav_section {
      .User_Part {
        display: none;
      }
      .Search_Part {
        display: none;
      }
      .icon-wrapper {
        position: relative;
        float: left;
        margin-top: 15px;
        padding: 10px;
        .badge {
          width: 20px;
          height: 20px;
          position: absolute;
          top: 10px;
          right: 0px;
        }
      }
    }
  }
}
