.Wrapper_content {
  padding: 0px 100px;
  display: flex;

  @include phone-small {
    padding: 50px 10px;
    display: block;
  }
  @include tablet-down {
    padding: 50px 10px;
    display: block;
  }

  .Wrapper_content_left {
    width: 25%;

    @include phone-small {
      width: 100%;
    }
    @include tablet-down {
      width: 100%;
    }

    .filter_box {
      display: block;
      margin-top: 35px;
      .cross {
        display: none;

        @include phone-small {
          display: inline-block;
          width: 14px;
          height: 14px;
        }
        @include tablet-down {
          display: inline-block;
          width: 14px;
          height: 14px;
        }
      }
      @include phone-small {
        background: white;
        box-shadow: 0 2px 8px rgb(0 0 0 / 25%);
        padding: 10px;
        height: 100vh;
        display: none;
        &.active {
          display: block;
        }

        .cross_and_filter {
          display: flex;
          justify-content: space-between;
        }
      }

      @include tablet-down {
        background: white;
        box-shadow: 0 2px 8px rgb(0 0 0 / 25%);
        padding: 10px;
        height: 100vh;
        display: none;
        &.active {
          background-color: #fff;
          display: block;
          height: 100%;
          left: 0;
          padding: 0;
          position: fixed;
          top: 0;
          width: 100%;
          margin-top: 0px;
        }
        .cross_and_filter {
          display: flex;
          justify-content: space-between;
          background-color: #e5e5e7;
          padding: 18px 18px 18px 20px;
        }
      }
    }
    .filter_results {
      display: none;

      @include phone-small {
        display: flex;
        justify-content: space-around;
        margin-top: 20px;
        margin-bottom: 20px;
      }

      @include tablet-down {
        display: flex;
        justify-content: space-around;
        margin-top: 20px;
        margin-bottom: 20px;
      }
    }

    p {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-normal) var(--unnamed-font-size-16) /
        var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
      letter-spacing: var(--unnamed-character-spacing-0);
      color: #545d63;
      text-align: left;
      font: normal normal normal 16px/24px Muli;
      letter-spacing: 0px;
      margin-top: 10px;
    }

    h5 {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-normal) var(--unnamed-font-size-20) / 14px
        var(--unnamed-font-family-muli);
      text-align: left;
      font: normal normal normal 20px/14px Muli;
      letter-spacing: 0px;
      color: #181c24;
      margin-top: 5px;
    }

    .horizontal_Line {
      width: 90%;
      height: 0px;
      border: 0.8px solid #d1d3d6;
      margin-top: 24px;
      @include phone-small {
        margin-top: 0px;
        width: 100%;
      }

      @include tablet-down {
        margin-top: 0px;
        width: 100%;
      }
    }
    .Filters {
      display: flex;
      justify-content: flex-start;
      width: 60%;
      margin-top: 22px;

      @include phone-small {
        padding: 0px 20px;
        margin-top: 22px;
      }

      @include tablet-down {
        padding: 0px 20px;
      }
      input[type="checkbox"] {
        height: 24px;
        position: relative;
        width: 24px;
      }
      input:checked:before {
        border-color: #1f39ff;
      }

      input {
        -webkit-appearance: none;
        appearance: none;
        height: 24px;
        position: relative;
        width: 24px;
        margin-right: 10px;
      }

      input:before {
        border: 2px solid #91959c;
        border-radius: 4px;
        content: "";
        display: block;
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        transition: all 0.3s ease-out;
        width: 100%;
      }

      input:checked:after {
        opacity: 1;
      }
      input:after {
        background: #fff url(../../assets/check.svg) no-repeat 100%;
        content: "";
        display: block;
        height: 16px;
        left: 9px;
        opacity: 0;
        position: absolute;
        top: 0;
        transition: all 0.3s ease-out;
        width: 19px;
      }
      p {
        padding-left: 10px;
        margin-top: 3px;
      }
    }
  }
  .Wrapper_content_right {
    width: 75%;
    @include phone-small {
      width: 100%;
    }

    @include tablet-down {
      width: 100%;
    }

    .Product_Sorting {
      display: flex;
      justify-content: space-between;
      h5 {
        display: none;
        @include phone-small {
          display: inline-block;
        }

        @include tablet-down {
          display: inline-block;
        }
      }
      .cross {
        display: none;
        @include phone-small {
          display: inline-block;
        }

        @include tablet-down {
          display: inline-block;
        }
      }
      @include phone-small {
        display: grid;
        place-content: center;
        .sort_box {
          width: 100%;
          background: white;
          display: flex;
          justify-content: space-around;
          flex-direction: row-reverse;
          box-shadow: 0 2px 8px rgb(0 0 0 / 25%);
          padding: 20px;
          display: none;
          &.active {
            background-color: #fff;
            display: block;
            height: 100%;
            left: 0;
            padding: 0;
            position: fixed;
            top: 0;
            width: 100%;
            margin-top: 0px;

            .cross_and_sort {
              display: flex;
              justify-content: space-between;
              background-color: #e5e5e7;
              border-bottom: 2px solid #d1d3d6;
              color: #181c24;
              padding: 18px 18px 18px 20px;

              h5 {
                font-size: 18px;
                line-height: 24px;
                font-weight: 400;
                font-family: muli,sans-serif;
              }
            }
          }
        }
      }

      @include tablet-down {
        display: grid;
        place-content: center;
        .sort_box {
          width: 100%;
          background: white;
          display: flex;
          justify-content: space-around;
          flex-direction: row-reverse;
          box-shadow: 0 2px 8px rgb(0 0 0 / 25%);
          padding: 20px;
          display: none;
          &.active {
            background-color: #fff;
            display: block;
            height: 100%;
            left: 0;
            padding: 0;
            position: fixed;
            top: 0;
            width: 100%;
            margin-top: 0px;

            .cross_and_sort {
              display: flex;
              justify-content: space-between;
              background-color: #e5e5e7;
              border-bottom: 2px solid #d1d3d6;
              color: #181c24;
              padding: 18px 18px 18px 20px;

              h5 {
                font-size: 18px;
                line-height: 24px;
                font-weight: 400;
                font-family: muli,sans-serif;
              }
            }
          }
        }
      }
      h6 {
        font: var(--unnamed-font-style-normal) normal
          var(--unnamed-font-weight-600) var(--unnamed-font-size-16) /
          var(--unnamed-line-spacing-20) var(--unnamed-font-family-muli);
        font: normal normal 600 16px/20px Muli;
        letter-spacing: 0px;
        color: #172026;
        margin-top: 10px;
      }
      select {
        background: #fff 0 0 no-repeat padding-box;
        border: 2px solid #b5b7bc;
        border-radius: 8px;
        color: #172026;
        font-size: 16px;
        height: 44px;
        outline: rgba(61, 132, 255, 0.749);
        width: 254px;
        height: 44px;
        
        @include phone-small {
          margin: 20px;
        }

        @include tablet-down {
          margin: 20px;
        }

        option {
          font: var(--unnamed-font-style-normal) normal
            var(--unnamed-font-weight-normal) var(--unnamed-font-size-16) /
            var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
          letter-spacing: var(--unnamed-character-spacing-0);
          color: var(--unnamed-color-172026);
          text-align: left;
          font: normal normal normal 16px/24px Muli;
          letter-spacing: 0px;
          color: #172026;
        }
      }
    }

    .Product_Listing {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;

      .Product_Data {
        width: 30%;
        padding-top: 24px;
        cursor: pointer;
        
        .wrap_image {
          background-color: #181c24;
          height: 382px;
          margin: auto;
          max-width: 319px;
          overflow: hidden;
          @include phone-small {
            height: 196px;
          }
  
          @include tablet-down {
            height: 196px;
          }

          img {
            height: 100%;
            object-fit: fill;
            width: 100%;
          }
        }
        h5 {
          padding-top: 8px;
          font: var(--unnamed-font-style-normal) normal
            var(--unnamed-font-weight-bold) 18px /
            var(--unnamed-line-spacing-20) var(--unnamed-font-family-muli);
          letter-spacing: var(--unnamed-character-spacing-0);
          color: var(--unnamed-color-172026);
          text-align: left;
          font: normal normal bold 18px/20px Muli;
          letter-spacing: 0px;
          color: #172026;
        }
        p {
          padding-top: 5px;
          font: var(--unnamed-font-style-normal) normal
            var(--unnamed-font-weight-600) var(--unnamed-font-size-16) /
            var(--unnamed-line-spacing-20) var(--unnamed-font-family-muli);
          letter-spacing: var(--unnamed-character-spacing-0);
          color: var(--unnamed-color-172026);
          text-align: left;
          font: normal normal 600 16px/20px Muli;
          letter-spacing: 0px;
          color: #172026;
        }
        i {
          padding-top: 17.5px;
          border: 2px solid #fff;
        }
        @include phone-small {
          width: 45%;
        }

        @include tablet-down {
          width: 45%;
        }

        img {
          width: 100%;
          height: 382px;

          @include phone-small {
            height: 196px;
          }
          @include tablet-down {
            height: 196px;
          }
        }
      }
    }
    .pagination {
      display: flex;
      justify-content: center;
      margin-top: 10px;

      a {
        color: black;
        float: left;
        padding: 8px 16px;
        text-decoration: none;
      }
    }
    .pagination_hide {
      display: none;
    }
  }
}
